.profile_container{display:flex;align-items:center;justify-content:center;flex-direction:column;font-family:"Source Sans Pro",sans-serif;padding-bottom:100px;margin-top:7%;background-color:#12c2e9;background:linear-gradient(to bottom, #f64f59, #c471ed, #12c2e9)}.profile_container p{color:#000;font-size:20px}.profile_circle{border-radius:50%;height:200px;display:flex;align-items:center;align-content:center;flex-direction:column;margin-left:20%;margin-top:10%;margin-bottom:0;margin-right:20%}.profile_circle img{border-radius:50%;width:200px;height:200px;object-fit:cover;z-index:9;position:absolute;margin-top:5%}.text-holder{width:400px;text-align:center;margin-bottom:50px}.profile-button-scroll{padding:20px;border-radius:10px;font-family:"Source Sans Pro",sans-serif;background-color:#a2d9ff;font-size:20px;color:#000}.top_wave{margin-top:0%;padding-bottom:20px;z-index:-1;position:absolute}
button{border:none;outline:none;text-decoration:none;text-align:center;text-transform:none;background-color:#7eb43c}a{color:#000;font-family:"Source Sans Pro",sans-serif}.button-component button{padding:20px;border-radius:10px;font-family:"Source Sans Pro",sans-serif;background-color:#ffef99;font-size:16px;font-weight:bold;color:#5c3f1f}.menu-container{position:-webkit-sticky;position:sticky;left:0;top:0;width:100%;top:10px;z-index:5}.menu-container ul{list-style:none}.menu-container li{font-size:20px;font-weight:bold;font-family:"Source Sans Pro",sans-serif;margin-right:10px;color:#fff;display:inline;cursor:pointer}img{width:300px;height:300px}.parent_square{height:210px;width:315px;margin:10px;display:flex;background-color:#000;justify-content:center;align-items:center;cursor:pointer;position:relative}.parent_square:hover .overlay{height:100%}.sliding_text{color:#000;opacity:1;font-size:20px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.overlay{position:absolute;bottom:0;left:0;right:0;background-color:#faf8f6;overflow:hidden;width:100%;height:0;transition:.5s ease;font-family:"Source Sans Pro",sans-serif}.square{height:200px;width:305px}.tile_container{display:flex;padding:20px;width:80%}.main_tile{margin:10px;display:flex;flex-direction:column;font-family:"Source Sans Pro",sans-serif;font-size:20px;min-width:90%;min-height:200px}.company{font-size:20px;font-weight:bold}.tile_image{float:right;margin:10px}.tile_image_container{max-width:20%}.tag_container{display:flex;justify-content:center}.tag{max-width:100px;font-size:15px;min-height:10px;background-color:#153a91;border:1px solid #000;margin:2px;color:#faf8f6;font-family:"Source Sans Pro",sans-serif}.details_holder{display:flex;justify-content:center}
.table{display:flex;align-items:center;justify-content:space-evenly;flex-direction:row;margin:auto auto 180px auto;flex-wrap:wrap;width:auto;height:auto;margin-left:0;margin-right:0;padding-top:20px}.projects-container{display:flex;background-color:#2f80ed;text-align:center}.panel-container{padding:10px}.section_title_projects{font-family:"Source Sans Pro",sans-serif;font-size:40px;border-bottom:2px solid #fff;color:#fff;background-color:#2f80ed;padding-top:12%;text-align:center}
.work_container{display:flex;justify-content:center;align-items:center;background-color:#2f80ed}.holder{display:flex;justify-content:center;align-items:center;flex-direction:column;bottom:0;left:0;right:0;margin-top:10%;width:auto;background-color:#000;width:70%;height:auto;color:#fff}img{width:100px;height:100px;float:right}.section_title{font-size:40px;margin:20px 0 20px 0;border-bottom:2px solid #fff}
.contact_container{background:#2f80ed;background:linear-gradient(to bottom, #2f80ed, #000046);display:flex;width:100%;align-items:center;flex-direction:column;justify-content:center;color:#fff;font-size:20px}.border{color:#fff;border:1px solid #fff;width:500px;margin-top:5px}.link_container{width:250px;height:200px;background-color:transparent;margin:10px 10px 10px 10px;flex-direction:row;justify-content:center;display:flex}.link_container img{width:50px;height:50px;margin:10px}
.background_layout{height:100vh;position:absolute;padding-top:30%}.layout_aboutme{margin:0;position:relative;background:#56ccf2;background:linear-gradient(to bottom, #13C1E9, #2F80ED)}.page_container{display:flex;justify-content:center;align-items:center;background:#56ccf2;background:linear-gradient(to bottom, #13C1E9, #2F80ED)}.padding_between_pages{margin-top:10%}.exact_container{display:flex;justify-content:center;align-items:center}.text_container{border-radius:20%;width:400px;height:600px;background-color:#a2d9ff;font-family:"Zen Kaku Gothic New",sans-serif;font-size:16px;color:#000;display:flex;align-items:center}.text_container p{border-radius:20%;width:280px;height:590px;padding:20px 20px 10px 40px;margin-left:20px}.picture_container{border-radius:20%;width:400px;height:600px;background-color:#a2d9ff;margin-right:10px}.picture_container img{object-fit:cover;border-radius:20%;width:400px;height:600px}.section_title--aboutMe{font-size:40px;margin:20px 0 20px 0;border-bottom:2px solid #fff;align-self:center;text-align:center}
